@use 'variables'as v;

html {
    font-size: 65.5%;
    box-sizing: border-box;

}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    &.fijar-body{
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 8.6rem;
    }
}

p {
    font-size: 2rem;
    line-height: 1.5;
    color: v.$negro;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1,
h2,
h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

img {
   max-width: 100%;
   width: 100%; //lo agregamos para que el 200 y 300 del html no desconfigure
   height: auto; //lo agregamos para que el 200 y 300 del html no desconfigure
   display: block;
}